<template>
  <div class="goods">
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>视频管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <el-card>
      <el-row>
        <el-col :span="6">
          <el-input v-model="queryInfo.query" placeholder="请输入内容">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="6" class="addsb">
          <el-button type="primary" @click="toAddGoods">添加用户</el-button>
        </el-col>
      </el-row>
      <!-- 表格 -->
      <el-table :data="goodsData" style="width: 100%" border>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label="商品名称"></el-table-column>
        <el-table-column prop="goods_price" label="商品价格"></el-table-column>
        <el-table-column prop="goods_number" label="商品数量"></el-table-column>
        <el-table-column prop="goods_weight" label="商品重量"></el-table-column>
        <el-table-column prop="add_time" label="创建时间">
          <template slot-scope="scop">{{scop.row.add_time | forDate}}</template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scop">
            <!-- {{scop.row}} -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="getGoodsListInfo(scop.row.goods_id)"
            ></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteGoods(scop.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[2, 5, 10, 15]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--修改 -->
      <el-dialog title="修改数据" :visible.sync="alterDia" width="30%" @closed="diaClosed('alterRef')">
        <el-form :model="alterForm" :rules="alterRul" ref="alterRef">
          <el-form-item label="商品名称" prop="goods_name">
            <el-input v-model="alterForm.goods_name"></el-input>
          </el-form-item>
          <el-form-item label="商品价格" prop="goods_price">
            <el-input v-model="alterForm.goods_price"></el-input>
          </el-form-item>
          <el-form-item label="商品数量" prop="goods_number">
            <el-input v-model="alterForm.goods_number"></el-input>
          </el-form-item>
          <el-form-item label="商品质量" prop="goods_weight">
            <el-input v-model="alterForm.goods_weight"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="alterDia = false">取 消</el-button>
          <el-button type="primary" @click="updataGoodsList">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      goodsData: [],
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 2
      },
      total: 0,
      alterDia: false,
      alterForm: {
        goods_id: "",
        goods_name: "",
        goods_price: "",
        goods_number: "",
        goods_weight: ""
      },
      alterRul: {
        goods_name: [
          { required: true, message: "请输入商品名称", trigger: "blur" }
        ],
        goods_price: [
          { required: true, message: "请输入商品价格", trigger: "blur" }
        ],
        goods_number: [
          { required: true, message: "请输入商品数量", trigger: "blur" }
        ],
        goods_weight: [
          { required: true, message: "请输入商品质量", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    async getGoodsList() {
      let { data: res } = await this.$http.get("goods", {
        params: this.queryInfo
      });
      if (res.meta.status != 200) {
        return this.$message.error("商品列获取失败");
      }
      this.total = res.data.total;
      this.goodsData = res.data.goods;
    },
    handleSizeChange(size) {
      this.queryInfo.pagesize = size;
      this.getGoodsList();
    },
    handleCurrentChange(num) {
      this.queryInfo.pagenum = num;
      this.getGoodsList();
    },
    // 获取商品信息
    async getGoodsListInfo(id) {
      this.alterDia = true;
      let { data: res } = await this.$http.get("goods/" + id);
      if (res.meta.status != 200) {
        return this.$message.error("获取商品信息失败");
      }
      this.alterForm = res.data;
    },
    async updataGoodsList() {
        let {data:res} = await this.$http.put('goods/'+this.alterForm.goods_id,this.alterForm)
        if(res.meta.status != 200){
            return this.$message.error("商品修改失败")
        }
        this.$message.success("商品修改成功")
        this.alterDia = false;
        this.getGoodsList()
    },
    // 删除商品
    async deleteGoods(id){
        let {data:res} = await this.$http.delete('goods/'+id)
        if(res.meta.status != 200){
            return this.$message,error('商品删除失败')
        }
        this.$message.success("商品删除成功")
        this.getGoodsList()
    },
    toAddGoods(){
        this.$router.push('/goodsAdd')
    },
    // 表单重置
    handleClose() {
      this.alterDia = false;
      this.$refs.alterRef.resetFields();
    }
  },
  created: function() {
    this.getGoodsList();
  }
};
</script>

<style>
</style>
